$transition-duration: 0.8s;
$transition-easing: cubic-bezier(0.230, 1.000, 0.320, 1.000);
$bounce-easing: cubic-bezier(0.175, 0.885, 0.320, 1.275);
$closed-width: 200px;
$closed-height: 80px;
$opened-width: 280px;
$opened-height: 140px;

.fc-btn.is-open~.description {
  opacity: 0;
}

.fc-btn {
  position: relative;
  transition: width $transition-duration $transition-easing,
    height $transition-duration $transition-easing,
    transform $transition-duration $bounce-easing;
  transform-style: preserve-3d;
  transform-origin: 50% 50%;
  text-align: center;
}

.fc-btn-front {
  position: absolute;
  display: block;
  cursor: pointer;
  backface-visibility: hidden;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  transition: background 0.15s ease,
    line-height $transition-duration $transition-easing;
}

.fc-btn-front:hover1 {
  background-color: lighten(#F44336, 10%);
}

.fc-btn.is-open .fc-btn-front {
  pointer-events: none;
  line-height: $opened-height;
}

.fc-btn-back {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #eee;
  color: #222;
  transform: translateZ(-2px) rotateX(180deg);
  overflow: hidden;
  transition: box-shadow $transition-duration ease;
}

.fc-btn-back p {
  margin-top: 27px;
  margin-bottom: 25px;
}

.fc-btn-back button {
  padding: 6px 12px;
  width: 30%;
  margin: 0 5px;
  font-size: 1em;
  cursor: pointer;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  transition: background 0.15s ease;

  &:focus {
    outline: 0;
  }

}

.fc-btn.is-open .fc-btn-back {
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

.fc-btn[data-direction="left"] .fc-btn-back,
.fc-btn[data-direction="right"] .fc-btn-back {
  transform: translateZ(-2px) rotateY(180deg);
}

.fc-btn.is-open {
  width: $opened-width;
  height: $opened-height;
}

.fc-btn[data-direction="top"].is-open {
  transform: rotateX(180deg);
}

.fc-btn[data-direction="right"].is-open {
  transform: rotateY(180deg);
}

.fc-btn[data-direction="bottom"].is-open {
  transform: rotateX(-180deg);
}

.fc-btn[data-direction="left"].is-open {
  transform: rotateY(-180deg);
}